	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-08-jeff-jonas-cops-and-robbers-las-vegas-style/index.md absPath of file >>> MarkdownRemark","html":"<p>Helped track down the MIT teams in Las Vegas.\nCorporate amnesia. Ie marketing sends credit card offer to a guy in jail who just defrauded the company. Companies have multiple database systems that need to coordinate.</p>\n<p>Data and queries are the same thing. Queries persist and you ask every smart question (query) all the time.</p>\n<p>Civil liberties and privacy become a big deal. What is appropriate to ask? What if the librarian – app putting stuff in or out is corrupt?</p>\n<p>You can anonomize – if I take a grinder and a pig you get sausage but if I give you sausage and a grinder you don’t get back a pig.</p>\n<p>Sequence neutrality. No matter what order your data arrives in your result is the same.</p>","frontmatter":{"title":"Jeff Jonas Cops and Robbers Las Vegas Style","date":"November 08, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/jeff-jonas-cops-and-robbers-las-vegas-style"}}

